:root {
    --clr-primary: #651fff;
    --clr-gray: #37474f;
    --clr-gray-light: #b0bec5;
}

* {
    box-sizing: border-box;
    font-family: "Open Sans", sans-serif;
    margin: 5px;
}

body {
    color: var(--clr-gray);
    margin-top: 10%;
}

.wrapper-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, 30rem);
    justify-content: center;
}

.container {
    overflow: hidden;
    box-shadow: 0px 2px 8px 0px var(--clr-gray-light);
    background-color: white;
    border-radius: 1rem;
    position: relative;
    margin: 0.5rem;
    padding: 5px;
}

.name {
    text-align: center;
    font-weight: bold;
    font-size: 1.5rem;
}

.description {
    margin: 1rem 2rem;
    font-size: 1rem;
}

.count {
    text-align: center;
    padding: 5px;
}
